import React from "react";
import axios from "axios";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { blocos: [], camarotes: [], navios: [], hoteis: [] };
  }
  componentDidMount() {
    axios
      .get("http://127.0.0.1:8080/blocos")
      .then(
        function(response) {
          this.setState({ blocos: response.data });
        }.bind(this)
      )
      .catch(function(error) {
        // handle error
        console.log(error);
      });

    axios
      .get("http://127.0.0.1:8080/camarotes")
      .then(
        function(response) {
          this.setState({ camarotes: response.data });
        }.bind(this)
      )
      .catch(function(error) {
        // handle error
        console.log(error);
      });
  }
  render() {
    return (
      <div>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
        <h1>HOME</h1>
      </div>
    );
  }
}

export default Home;
